import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Card from '../models';

export default {
  name: 'card',

  match(node: JsonML): boolean {
    return getTagName(node) === 'card';
  },

  convert(node: JsonML): Block {
    return Card.create({
      data: {
        cardType: '',
        ...getAttributes(node),
      },
    });
  },
};
